@import url(https://fonts.googleapis.com/css?family=Lato|Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Open Sans', sans-serif; }

.navbar-main {
  background-color: #0057b8; }
  .navbar-main .navbar-toggler-icon {
    color: #fff;
    border-color: transparent; }
  .navbar-main .navbar-toggler {
    color: #fff;
    border-color: transparent; }
  .navbar-main .nav-item a {
    padding: 0.5rem 1rem;
    color: #fff;
    display: block; }
    .navbar-main .nav-item a:active, .navbar-main .nav-item a:hover {
      background-color: rgba(255, 255, 255, 0.2);
      text-decoration: none; }

.page .nav-tabs {
  border-bottom: none;
  display: flex;
  margin-bottom: 15px; }
  .page .nav-tabs .nav-item {
    flex: 1 1;
    margin-bottom: 0; }
    .page .nav-tabs .nav-item .nav-link {
      text-transform: uppercase;
      border: none;
      text-align: center;
      border-bottom: 3px solid transparent; }
      .page .nav-tabs .nav-item .nav-link.active {
        color: #0057b8;
        border-bottom: 3px solid #0057b8;
        font-weight: bold; }

.page .donation-progress {
  margin-top: 1rem; }

.page p {
  line-height: 1.88;
  opacity: 0.7; }

.page .story-headline {
  color: #0057b8; }

.page .profile-header {
  display: flex;
  align-items: center;
  margin-bottom: 0.75rem; }
  .page .profile-header h2 {
    margin-right: 15px; }
  .page .profile-header .age {
    font-size: 1.4rem;
    flex: 1 1;
    opacity: 0.65; }
  .page .profile-header .condition {
    font-size: 0.8rem;
    color: #fff;
    background-color: #0057b8;
    border-radius: 5px;
    padding: 0.3rem; }

.page .sponsor-header h2 {
  margin-top: 1rem; }

.page .vertical-btn-container {
  display: flex;
  flex-direction: column; }
  .page .vertical-btn-container button {
    margin-top: 1rem;
    border-radius: 0;
    border-color: transparent;
    padding: 1rem 2rem;
    text-transform: uppercase;
    font-weight: bold; }
    .page .vertical-btn-container button.rest-of-goal {
      background-color: #0057b8; }

.page .story {
  padding-bottom: calc(1rem + 58px); }

@-webkit-keyframes pulse {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.5; }
  100% {
    opacity: 1; } }

@keyframes pulse {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.5; }
  100% {
    opacity: 1; } }

.page .you {
  -webkit-animation: pulse 2.5s infinite ease-in-out;
  animation: pulse 2.5s infinite ease-in-out; }

.page .key {
  margin-bottom: 2rem; }
  .page .key .you {
    margin-left: 5px; }

.page .update-warning {
  opacity: 0.5;
  text-align: center;
  font-size: 1.5rem;
  margin-top: 1rem; }

.page .donation-card {
  margin-top: 1rem;
  background-color: #009cb8;
  color: #fff;
  border-color: transparent; }
  .page .donation-card.other {
    background-color: #fff;
    color: #009cb8;
    border-color: #009cb8; }
  .page .donation-card span {
    margin-left: 1rem; }

.page .wish-container {
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 5px;
  color: #0057b8;
  font-weight: bold;
  padding: 1rem;
  padding-bottom: calc(1rem + 58px); }
  .page .wish-container .wish {
    margin-top: 1rem;
    opacity: 1;
    text-align: center; }

.btn-container {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex; }
  .btn-container button {
    border-radius: 0;
    border-color: transparent;
    padding: 1rem 2rem;
    text-transform: uppercase;
    font-weight: bold; }
  .btn-container #back-btn {
    background-color: #0057b8; }
  .btn-container #sponsor-btn {
    background-color: #ff585d;
    flex: 1 1; }

.gradient {
  background: linear-gradient(270deg, #0057b8, #00b899);
  background-size: 400% 400%;
  -webkit-animation: gradient 5s ease infinite;
  animation: gradient 5s ease infinite; }

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

.my-account-container h2 {
  margin-top: 1rem;
  font-size: 1.5rem; }

.my-account-container .btn-container-account {
  display: flex; }
  .my-account-container .btn-container-account button {
    flex: 1 1;
    padding: 1rem;
    border-radius: 0; }

.my-account-container .total-donations-card {
  margin-top: 2rem;
  margin-bottom: 2rem;
  background-color: #009cb8;
  color: #fff;
  border-color: transparent; }
  .my-account-container .total-donations-card .card-title {
    text-transform: uppercase;
    opacity: 0.7; }
  .my-account-container .total-donations-card h2 {
    font-size: 2rem;
    font-weight: bold;
    margin: 0; }

.my-account-container .recent-donations-card {
  margin-top: 2rem;
  margin-bottom: 15px; }
  .my-account-container .recent-donations-card .card-title {
    text-transform: uppercase;
    opacity: 0.7; }
  .my-account-container .recent-donations-card h2 {
    font-size: 2rem;
    font-weight: bold;
    margin: 0; }

.my-account-container .rccs {
  margin-top: 2rem;
  margin-bottom: 2rem; }

.my-account-container h6 {
  text-align: center; }

.my-account-container .transaction-row {
  display: flex;
  align-items: center; }
  .my-account-container .transaction-row .transaction-purchase {
    flex: 1 1; }
  .my-account-container .transaction-row .transaction-date {
    margin-left: 5px;
    opacity: 0.6;
    font-size: 0.7rem; }
  .my-account-container .transaction-row .transaction-amount {
    min-width: 80px;
    text-align: right; }
  .my-account-container .transaction-row .transaction-donation {
    min-width: 80px;
    text-align: right; }
  .my-account-container .transaction-row.transaction-header {
    opacity: 0.5;
    font-size: 0.7rem;
    text-transform: uppercase; }
    .my-account-container .transaction-row.transaction-header .transaction-donation {
      font-weight: normal;
      font-weight: initial; }

.child-container .child-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  padding-left: 0;
  border-bottom: 1px solid #e6e6e6; }
  .child-container .child-row .cell.first-name {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 1.2rem; }
    .child-container .child-row .cell.first-name .star-btn {
      background-color: transparent;
      border-color: transparent; }
  .child-container .child-row .cell.condition {
    background-color: #009cb8;
    color: #fff;
    font-size: 0.8rem;
    padding: 0.1rem 0.3rem;
    border-radius: 5px; }
  .child-container .child-row:hover {
    cursor: pointer; }
  .child-container .child-row:active {
    background-color: #0056b82a; }

.congrats-page {
  background-color: #1f77b5;
  min-height: calc(100vh - 56px); }
  .congrats-page h6 {
    color: #fff;
    text-align: center;
    font-size: 1.2rem; }
    .congrats-page h6 span {
      font-size: 2rem;
      color: #50e3c2; }
  .congrats-page #youtube-vid {
    max-width: 100%; }
  .congrats-page .wish-reveal {
    background-color: #ff585d;
    border-radius: 25px;
    text-transform: uppercase;
    margin-top: 2rem; }
  .congrats-page .congrats-col {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 56px); }

#go-back-btn {
  position: fixed;
  bottom: 0;
  width: 100%;
  border-radius: 0;
  border-color: transparent;
  padding: 1rem 2rem;
  text-transform: uppercase;
  font-weight: bold;
  background-color: #ff585d; }

